import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';
import { HelpdeskReportsDataComponent } from './helpdesk-reports-data/helpdesk-reports-data.component';
import { HelpdeskReportsComponent } from './helpdesk-reports.component';

const routes: Routes = [
  { path: '', component: HelpdeskReportsComponent, canActivate: [AuthGuard]},
	{ path: ':name/:id', component: HelpdeskReportsDataComponent, canActivate: [AuthGuard] },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpdeskReportsRoutingModule { }
